import { subItemLabel } from './../../model/subItemLabel';
import { appSetting } from 'src/app/app-setting';
import { UploadService } from './../upload/upload.service';
import { Injectable } from "@angular/core";
import { subItemModel } from "src/app/model/subItemModel";
import { categoryModel } from "src/app/model/categoryModel";
import { Observable } from "rxjs";
import { HttpHeaders } from "@angular/common/http";
import { HttpClient } from "@angular/common/http";
import Swal from 'sweetalert2';

@Injectable({
  providedIn: 'root'
})
export class SubitemService {
  private url = `${this.appSetting.http}://${this.appSetting.apiPort}/api/subItemModels`;
  private labelurl = `${this.appSetting.http}://${this.appSetting.apiPort}/api/subItemLabel`;
  private httpOptions = {
    headers: new HttpHeaders({
      "Content-Type": "application/json"
    })
  };
  constructor(private http: HttpClient,private uploadService:UploadService,private appSetting:appSetting) {}


  get(id:number): Observable<subItemModel[]> {
    const searchUrl = `${this.url}/${id}`;
    return this.http.get<subItemModel[]>(searchUrl);
  }

  getSubItemLabel(id:number):Observable<subItemLabel[]> {
    const searchUrl = `${this.labelurl}/${id}`;
    return this.http.get<subItemLabel[]>(searchUrl);
  }
  getSingle(id: number): Observable<subItemModel> {
    const searchUrl = `${this.url}/${id}`;
    return this.http.get<subItemModel>(searchUrl);
  }
  post(data: subItemModel,uploadData:FormData,selectedFile:File){
    var obj;
    let x:subItemModel;
    this.http.post(this.url, data, this.httpOptions).subscribe(
      res => {
        obj=res;
        console.log(res);
      },
      err => {
        console.log(err);
      },
      () => {
        x=Object.assign(obj);
        this.uploadService.upload(uploadData,selectedFile,"Sub-"+x.sub_item_id.toString()+"-"+x.sub_item_image);
      }
    );
  }

  put(data: subItemModel): void {
    const searchUrl = `${this.url}/${data.sub_item_id}`;
    this.http.put(searchUrl, data, this.httpOptions).subscribe(
      res => {
        console.log(res);
      },
      err => {
        console.log(err);
      }
    );
  }

  delete(main: number,sub:number,image:string): void {
    var data = null;
    var xhr = new XMLHttpRequest();
    xhr.withCredentials = false;

    xhr.addEventListener("readystatechange", function() {
      if (this.readyState === this.DONE) {
        console.log(this.responseText);
      }
    });

    xhr.open("DELETE", this.url + "?main="+main+"&sub="+sub+"&img=" + image);
    xhr.send(data);
    Swal.fire("Success","Your Process is successfully","success");
  }
}
