import { mainItemModel } from './../../model/mainItemModel';
import { Observable } from 'rxjs';
import { appSetting } from './../../app-setting';
import { HttpClient } from '@angular/common/http';
import { HttpHeaders } from '@angular/common/http';
import { Injectable } from '@angular/core';

@Injectable({
  providedIn: 'root'
})
export class GeneralSearchService {
  private url = `${this.appSetting.http}://${this.appSetting.apiPort}/api/generalSearch`;
  private httpOptions = {
    headers: new HttpHeaders({
      'Content-Type': 'application/json'
    })
  };
  constructor(private http: HttpClient,private appSetting:appSetting) {}
  get(name: string): Observable<mainItemModel[]> {
    const searchUrl = `${this.url}/?item=${name}`;
    return this.http.get<mainItemModel[]>(searchUrl);
  }
}
