import { appSetting } from 'src/app/app-setting';
import { Location } from '@angular/common';
import { HttpClient } from "@angular/common/http";
import { Injectable } from "@angular/core";
import { FileTransfer, FileUploadOptions, FileTransferObject } from '@ionic-native/file-transfer/ngx';
import Swal from 'sweetalert2';
@Injectable({
  providedIn: "root"
})

export class UploadService {
  constructor(private transfer: FileTransfer,private http: HttpClient,private history:Location,private appSetting:appSetting) {}
 
  upload(uploadData: FormData, selectedFile: File, filename: String) {
 this.appSetting.errorMsg.push('Selected file name'+JSON.stringify(selectedFile));
 this.appSetting.errorMsg.push('Selected blob name'+JSON.stringify(this.appSetting.selectedBlob));
    try {
     
     
       uploadData.append("myFile",selectedFile, selectedFile.name.toString());
      this.http
        .post(
          `${this.appSetting.http}://${this.appSetting.apiPort}/api/upload` +
            "?filename=" +
            filename,
          uploadData,
          {
            reportProgress: true,
            observe: "events"
          }
        )
        .subscribe(
          event => {
            console.log(event);
            this.appSetting.errorMsg.push("This is event for upload file-"+JSON.stringify(event));
          },
          err => {console.log(err);
            this.appSetting.errorMsg.push("This is error for upload fail-"+JSON.stringify(err));
            Swal.hideLoading();
            Swal.fire('Image Upload Fail','','error');
          },
          () => {
            console.log(event);
            this.appSetting.errorMsg.push("This is successful msg for upload file-"+JSON.stringify(event));
            this.appSetting.crud=0;
            this.appSetting.crudProcess='';
            Swal.hideLoading();

          }
        );
    } catch (e) {
      
      Swal.hideLoading();
      Swal.fire('Image Upload try/catch Fail',e,'error');
      this.appSetting.errorMsg.push(e.toString());
  
    }
  }
  
  uploadV2(uploadData: FormData ,filename: String) {
    
       try {
        
     
       
         this.http
           .post(
             `${this.appSetting.http}://${this.appSetting.apiPort}/api/upload` +
               "?filename=" +
               filename,
             uploadData,
             {
               reportProgress: true,
               observe: "events"
             }
           )
           .subscribe(
             event => {
               console.log(event);
               this.appSetting.errorMsg.push("This is event for upload file-"+JSON.stringify(event));
             },
             err => {console.log(err);
               this.appSetting.errorMsg.push("This is error for upload fail-"+JSON.stringify(err));
               Swal.hideLoading();
               Swal.fire('Image Upload Fail',err.toString(),'error');
             },
             () => {
               console.log(event);
               this.appSetting.errorMsg.push("This is successful msg for upload file-"+JSON.stringify(event));
               this.appSetting.crud=0;
               this.appSetting.crudProcess='';
               Swal.hideLoading();
   
             }
           );
       } catch (e) {
         
         Swal.hideLoading();
         Swal.fire('Image Upload Fail',e,'error');
         this.appSetting.errorMsg.push(e.toString());
     
       }
     }
}
