import { Location } from '@angular/common';
import { appSetting } from "src/app/app-setting";
import { UploadService } from "./../upload/upload.service";
import { Injectable } from "@angular/core";
import { mainItemModel } from "src/app/model/mainItemModel";
import { categoryModel } from "src/app/model/categoryModel";
import { Observable } from "rxjs";
import { HttpHeaders } from "@angular/common/http";
import { HttpClient } from "@angular/common/http";
import { isDefined } from '@angular/compiler/src/util';
import Swal from 'sweetalert2';
@Injectable({
  providedIn: "root"
})
export class MainitemService {
  private url = `${this.appSetting.http}://${this.appSetting.apiPort}/api/mainItemModels`;
  private Searchurl = `${this.appSetting.http}://${this.appSetting.apiPort}/api/searchItem`;
  private httpOptions = {
    headers: new HttpHeaders({
      "Content-Type": "application/json"
    })
  };
  constructor(
    private http: HttpClient,
    private uploadService: UploadService,
    private appSetting: appSetting,
    private history: Location
  ) { }
  get(): Observable<mainItemModel[]> {
    return this.http.get<mainItemModel[]>(this.url);
  }
  getSingle(id: number): Observable<mainItemModel> {
    const searchUrl = `${this.url}/${id}`;
    return this.http.get<mainItemModel>(searchUrl);
  }

  getMany(id: number): Observable<mainItemModel[]> {
    const searchUrl = `${this.Searchurl}/${id}`;
    return this.http.get<mainItemModel[]>(searchUrl);
  }

  getScrollDown(id: number,page:number): Observable<mainItemModel[]> {
    const searchUrl = `${this.Searchurl}/${id}`+`?page=${page}`;
    return this.http.get<mainItemModel[]>(searchUrl);
  }
  post(data: mainItemModel, uploadData: FormData, selectedFile: File) {
    var obj;
    let x: mainItemModel;
    this.http.post(this.url, data, this.httpOptions).subscribe(
      res => {
        obj = res;
      },
      err => { },
      () => {
        x = Object.assign(obj);
        this.uploadService.upload(
          uploadData,
          selectedFile,
          "Main-" + x.main_item_id.toString() + "-" + x.image_url
        );
        this.appSetting.mainItemID = x.main_item_id;

      }
    );
  }

  put(data: mainItemModel, uploadData: FormData, selectedFile: File): void {
    var obj;
    let x: mainItemModel;
    const searchUrl = `${this.url}/${data.main_item_id}`;
    this.http.put(searchUrl, data, this.httpOptions).subscribe(
      res => {
        console.log(data);

      },
      err => {
        console.log(err);
      },
      () => {
        x = data;
        this.uploadService.upload(
          uploadData,
          selectedFile,
          "Main-" + x.main_item_id.toString() + "-" + x.image_url
        );
        this.appSetting.mainItemID = x.main_item_id;

      }
    );
  }

  delete(id: number): void {
    var data = null;
    var xhr = new XMLHttpRequest();
    xhr.withCredentials = false;


    xhr.addEventListener("readystatechange", function () {
      if (this.readyState === this.DONE) {
        console.log(this.responseText);
      }
    });

    xhr.open("DELETE", this.url + "/" + id);

    xhr.send(data);
    Swal.fire("Success", "Your Process is successfully", "success").then(() => { this.history.back(); });

  }
}
